/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.msk-tabs .nav-tabs .tabAnchor {
  display: flex;
  align-items: center; }
  .msk-tabs .nav-tabs .tabAnchor .closeButton {
    margin-left: 10px;
    cursor: pointer; }
    .msk-tabs .nav-tabs .tabAnchor .closeButton:hover {
      opacity: 0.6; }
